	module.exports = {"data":{"post":{"html":"<p>We're going on an involuntary hiatus. Something has gone wrong with our change in telekom provider, and we have no telephone, no ISDN, and no Internet at home. I'm sure that Deutsche Telekom is working feverishly to fix the problem. Meanwhile we can only be reached by cell phone or at work. Maybe this weekend we'll watch a movie. Or read a book. </p>\n<p><strong>Update</strong> I should have never doubted the Deutsche Telekom. Within in hour the ISDN line was up. In a few days we might even have DSL.</p>","fields":{"title":"Hole In The Head","date":"04 Jun 2004","url":"https://papascott.de/2004-06-04-hole-in-the-head/"}}},"pathContext":{"slug":"/2004-06-04-hole-in-the-head/","prev":{"fields":{"title":"Futility in our time","date":"04 Jun 2004","slug":"/2004-06-04-futility-in-our-time/"}},"next":{"fields":{"title":"A Million Days","date":"05 Jun 2004","slug":"/2004-06-05-a-million-days/"}}}}

